.PostShare{
    display: flex;
    gap: 1rem;
    background-color: var(--cardColor);
    padding: 1rem;
    border-radius: 1rem;
    /* z-index: 1 !important; */
    
    

}

.PostShare>img{
    border-radius: 50%;
    width: 3rem;
    height: 3rem;
    
}
.PostShare>div{
    display: flex;
    flex-direction: column;
    width: 90%;
    gap: 1rem;

}
.PostShare>div>input{
    min-width: 100px;
    background-color: var(--inputColor);
    border-radius: 10px;
    padding: 10px;
    font-size: 17px;
    border: none;
    /* outline: none; */
}

.postOptions{
    display: flex;
    justify-content: space-around;

}
.option{
    padding: 5px;
    padding-left: 10px;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 12px;

}
.option:hover{
    cursor: pointer;

}
.ps-button{
    padding: 5px;
    padding-left: 20px;
    padding-right: 20px;
    font-size: 12px; 
}

.previewImage{
    position: relative;

}
.previewImage>svg{
    position: absolute;
    right: 1rem;
    top: 0.5rem;
    cursor: pointer;
    color: white;
}

.previewImage>img{
    width: 100%;
    max-height: 20rem;
    object-fit: cover;
    border-radius: 0.5rem;
}


@media screen and (max-width:1226px) {
    .PostShare{
        width: 90%;   
    }
   .PostShare>div>input{
    width: 90%;
   }
}

@media screen and (max-width:900px) {
    .PostShare{
        width: 400px;   
    }
    .option>span{
        display: none;
        justify-content: flex-start;
        
    }
}


@media screen and (max-width:768px) {
    .PostShare{
        display: flex;
        width: auto;
        
    }
}


