.InfoCard{
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    background-color: var(--cardColor);
    padding: 1rem;
    border-radius: 1rem;
    width: 90%;

}

.infoHead{
   display: flex;
   justify-content: space-between;
   align-items: center;
}

.infoHead>div:hover{
    cursor: pointer;

}

.logout-button{
    width: 7rem;
    height: 2rem;
    margin-top: 6rem;
    align-self: flex-end;
}


@media screen and (max-width:1200px){
    .InfoCard{
      grid-template-columns: 35% auto 20%;
 
    }
 
    .InfoCard::-webkit-scrollbar{
      display: none;
 
    }
 }

 @media screen and (max-width:768px){
    .InfoCard{
     width: 50%;
 
    }
 
 }


 @media screen and (max-width:520px){
    .InfoCard{
     width: 50%;
 
    }
 
 }